import React from 'react'

const formOpciones = [
	{
		name: 'Ttulo entrada',
		props: {
			name: 'titulo',
			type: 'text'
		}
	},
	{
		name: 'Foto portada',
		props: {
			name: 'fotoPortada',
			type: 'file',
			acept: 'image/*'
		}
	},
	{
		name: 'Texto introductorio',
		props: {
			type: 'textarea',
			name: 'intro'
		}
	},
	{
		name: 'ID del Video',
		props: {
			name: 'video',
			type: 'text',
		}
	},
	{
		name: 'Fotos galera',
		props: {
			name: 'galeria',
			type: 'file',
			acept: 'image/*',
			multiple: true,
		}
	}
]

const Administrador = (props) => (
	<form id="form-entrada" onSubmit={ props.onAddInput } >
		{
			formOpciones.map( (input, i) => {
				if (input.props.type === "textarea") {
					return (
						<div key={i}>
							<label >{input.name} <br/>
								<textarea {...input.props}/>
							</label>
						</div>
					)
				} else {
					return (
						<div key={i}>
							<label >{input.name} <br/>
								<input {...input.props} />
							</label>
						</div>
					)
				}
			}
		)
	}

	<input type="submit" />

	</form>
)

export default Administrador;


