import React, { Component } from 'react';

// eslint-disable-next-line
import firebase, {database, storage, auth} from 'firebase';

import FileUpload from './FileUpload'
import Administrador from './Administrador'

import './App.css';

class App extends Component {
  constructor () {
    super()
    this.state = {
      user: null,
      pictures: [],
      uploadValue: 0,
      entradas: []
    }

    this.handleUpload = this.handleUpload.bind(this);
    this.handleRemovePicture = this.handleRemovePicture.bind(this);
    this.renderEntradas = this.renderEntradas.bind(this);
  
  }

  componentWillMount () {
    auth().onAuthStateChanged(user => {
      this.setState({ user })
    })

    database().ref('pictures').on('child_added', snapshot => {
      this.setState({
        pictures: this.state.pictures.concat(snapshot.val())
      })
    })

    database().ref('pictures').on('child_removed', snapshot => {
      this.setState({
        pictures: this.state.pictures.filter( item => item.key !== snapshot.key )
      })
    })

    database().ref('entradas').on('child_added', snapshot => {
      this.setState({
        entradas: this.state.entradas.concat(snapshot.val())
      })
    })

    database().ref('entradas').on('child_removed', snapshot => {
      this.setState({
        pictures: this.state.entradas.filter( item => item.key !== snapshot.id )
      })
    })
  }
    
  handleAuth () {
    const provider = new auth.GoogleAuthProvider();
    auth().signInWithPopup(provider)
      .then(result => console.log(`${result.user.email} ha iniciado sesin`))
      .catch(err => console.log(`Error ${err.code}: ${err.message}`))
  }

  handleLogout () {
    auth().signOut()
      .then(result => console.log(`has salido`))
      .catch(err => console.log(`Error ${err.code}: ${err.message}`))
  }

  handleUpload (event) {
    const file = event.target.files[0];
    if (file) {
      const storageRef = storage().ref( `/fotos/${file.name}`)
      const task = storageRef.put(file)

      task.on('state_changed', snapshot => {
        let percentage = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        this.setState({
          uploadValue: percentage
        })
      }, error => {
        console.log(error.message)
      }, () =>{
        const dbRef = database().ref('pictures')
        const newPicture = dbRef.push()

        const record = {
          photoURL: this.state.user.photoURL,
          displayName: this.state.user.displayName,
          image: task.snapshot.downloadURL,
          key: newPicture.key,
          fileName: file.name
        }        
        newPicture.set(record);
      })
    }
  }

  handleRemovePicture (picture) {
    storage().ref('fotos/' + picture.fileName).delete()
    database().ref('pictures/' + picture.key).remove()
  }

  handleAddInput(e){
    e.preventDefault()
    let form = e.target,
        entrada = {
          titulo: form.titulo.value,
          intro: form.intro.value,
          fotoPortada: form.fotoPortada.files,
          videoURL: 'https://www.youtube.com/embed/' + form.video.value,
          fotosGaleria: form.galeria.files
        }

    
    const dbRef = database().ref('entradas'),
          nuevaEntrada = dbRef.push(),
          storageRef = storage().ref( `images/${entrada.titulo}`),
          fotoPortadaRef = storageRef.child(`portada_${entrada.fotoPortada[0].name}`)
    let record = {
      id: nuevaEntrada.key,
      titulo: entrada.titulo,
      introTXT: entrada.intro,
      videoURL: entrada.videoURL,
      portadaURL: "",
      galeria: []
    }    

    fotoPortadaRef.put(entrada.fotoPortada[0]).then( snapPortada => {

      record.portadaURL = snapPortada.downloadURL

      let filesUploaded = 0

      for (let i = 0; i < entrada.fotosGaleria.length; i++) {
        const fotoRef = storageRef.child(`${entrada.fotosGaleria[i].name}`)

        fotoRef.put(entrada.fotosGaleria[i]).then( snapFotoGaleria => {
          record.galeria.push({
            fotoURL: snapFotoGaleria.downloadURL,
            fotoName: snapFotoGaleria.metadata.name
          })
          
          filesUploaded++          

          if (filesUploaded === entrada.fotosGaleria.length) {
            nuevaEntrada.set(record)
            form.reset()
          }
        }).catch(err=>{
          console.log('error en galeria: '+ err)
        })
      }

    })
  }

  renderLoginButton() {
    //logeado
    if (this.state.user) {
      return (
        <div>
          <img src={this.state.user.photoURL} width="100" alt={this.state.user.displayName}/>
          <p>Hola {this.state.user.displayName}</p>
          <button onClick={ this.handleLogout }>Logout</button>
        </div>
      )
    } else {
      //no Logeado      
      return(
        <div>
          <button onClick={ this.handleAuth }>Login</button>
        </div>
      )
    }
  }

  renderEntradas () {
    if (this.state.entradas.length === 0) {
      return (
        <div>No hay entradas</div>
      )
    } else {
      return(
          this.state.entradas.map( entrada => (
            <div key={entrada.id}>
              <img src={entrada.portadaURL} alt="" width="100"/>
              <h2>{entrada.titulo}</h2>
              <p>{entrada.introTXT}</p>
              <i>{entrada.galeria.length} fotos en galera</i> <br/>
              <iframe title={entrada.key} src={entrada.videoURL} width="300" frameBorder="0"></iframe>
              <ul>
                {
                  entrada.galeria.map( (foto, i) =>(
                    <img key={i} src={foto.fotoURL} alt="" width="90"/>
                  ))
                }
              </ul>
              <button onClick={ (e) => {
                database().ref('entradas/' + entrada.id).remove()
              }} >eliminar</button>
            </div>
          ))
      )
    }
  }

  render() {
    return (
      <div className="App">
        { this.renderLoginButton() }
        <FileUpload onUpload={ this.handleUpload } uploadValue={ this.state.uploadValue}/>
        <Administrador onAddInput={ this.handleAddInput } />
        {
          this.state.pictures.map( (picture, index) => (
            <div key={picture.key}>
              <img src={picture.image} alt="" width="320" />
              <br/>
              <img src={picture.photoURL} alt={picture.displayName} width="50"/>
              <span>{ picture.displayName }</span>
              <button onClick={ () => this.handleRemovePicture(picture) } >delete</button>
            </div>
          )).reverse()
        }
        { this.renderEntradas() }
      </div>
    );
  }
}

export default App;



