import React from 'react';


const FileUpload = (props) => (
      <div>
        <progress value={props.uploadValue} max="100">{props.uploadValue}%</progress>
        <br/>
        <input type="file" onChange={props.onUpload}/>
      </div>
)

export default FileUpload;


